/*********************
VARIABLES
*********************/
article h1 {
  font-size: 50px;
  line-height: 60px; }
  @media (max-width: 640px) {
    article h1 {
      font-size: 24px;
      line-height: 24px; } }

article .time-address {
  color: #575e62;
  font-size: 1rem;
  font-weight: 400;
  margin: 16px 0px; }
  article .time-address address {
    display: inline-block;
    font-style: normal; }
  article .time-address time {
    display: inline-block; }
    article .time-address time:first-letter {
      text-transform: capitalize; }

article p {
  font-size: 18px;
  line-height: 28px; }
  article p.advisory {
    color: #e88063; }

article h2 {
  cursor: pointer;
  text-transform: capitalize; }

article h3 {
  text-transform: capitalize; }

@media screen {
  article.document {
    background-color: white;
    padding: 30px;
    overflow-y: auto;
    overscroll-behavior-y: contain; } }
  @media screen and (max-width: 640px) {
    article.document {
      padding: 15px; } }

@media screen {
    article.document figure {
      margin-left: -30px;
      margin-right: -30px;
      margin-top: -30px;
      background-color: #243447; } }
    @media screen and (max-width: 640px) {
      article.document figure {
        margin-left: -15px;
        margin-right: -15px;
        margin-top: -15px; } }

@media screen {
    article.document main {
      max-width: 800px;
      margin: auto; }
    article.document .actions {
      position: fixed;
      top: 8px;
      right: 8px; }
      article.document .actions button {
        background-color: rgba(0, 0, 0, 0.4); }
        article.document .actions button i {
          color: white; } }

@media print {
  figure {
    margin: 0; } }

@media screen {
  .night-mode article {
    background-color: #231f20; }
    .night-mode article h1, .night-mode article h2, .night-mode article h3 {
      color: white; }
    .night-mode article address, .night-mode article time {
      color: #a3acb1; }
    .night-mode article p {
      color: white; }
      .night-mode article p.advisory {
        color: #e88063; } }
