.media-gallery figure {
  position: relative;
  margin: 0; }
  .media-gallery figure > *:not(.placeholder) {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 0px;
    width: 100%; }

.media-gallery nav {
  display: flex;
  justify-content: center; }
  @media print {
    .media-gallery nav {
      display: none; } }
  .media-gallery nav ul {
    padding: 0;
    max-width: 80%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    list-style-type: none; }
    .media-gallery nav ul li {
      margin: 0 3px;
      cursor: pointer; }
      .media-gallery nav ul li.active {
        border-bottom: 4px solid #1DA1F2;
        cursor: default; }
      .media-gallery nav ul li img {
        width: 48px; }

.media-gallery p {
  margin-top: 18px;
  padding: 0 30px;
  color: #90979c;
  line-height: 1.5rem;
  font-size: 0.80rem; }
