/*********************
VARIABLES
*********************/
article.document .actions {
  position: absolute;
  top: 8px;
  right: 8px; }
  article.document .actions button {
    background-color: rgba(0, 0, 0, 0.4); }
    article.document .actions button i {
      color: white; }

article.document aside {
  margin-top: 24px; }
  article.document aside h1 {
    font-size: 2rem;
    line-height: 2.2rem; }
    @media (max-width: 640px) {
      article.document aside h1 {
        font-size: 24px;
        line-height: 24px; } }
  article.document aside p {
    font-size: 18px;
    line-height: 28px; }
    article.document aside p.advisory {
      color: #e88063; }
  article.document aside .time-address {
    color: #575e62;
    font-size: 1rem;
    font-weight: 400;
    margin: 18px 0px; }
    article.document aside .time-address .creator {
      display: inline-block;
      font-style: normal;
      text-transform: capitalize; }
    article.document aside .time-address time {
      display: inline-block; }
      article.document aside .time-address time:first-letter {
        text-transform: capitalize; }

@media screen {
  article {
    background-color: #243447;
    overflow: hidden; }
    article .figure {
      cursor: zoom-out;
      height: 100%;
      width: 100%; }
      article .figure.small {
        cursor: zoom-in;
        height: 50%; }
    article aside {
      position: absolute;
      bottom: 0px;
      right: 0px;
      padding: 30px;
      overflow-y: auto;
      overscroll-behavior-y: contain;
      background-color: white;
      padding: 30px;
      transition: transform .3s ease-in-out;
      max-height: 70%;
      max-width: 704px; } }
    @media screen and (max-width: 640px) {
      article aside {
        padding: 15px; } }

@media screen {
  .slide-enter,
  .slide-leave-active {
    transform: translateX(100%); } }

@media print {
  aside {
    display: block !important; } }

@media screen {
  .night-mode article {
    background-color: #2e2b2c; }
    .night-mode article aside {
      background-color: #231f20; }
      .night-mode article aside h1, .night-mode article aside h2 {
        color: white; }
      .night-mode article aside address, .night-mode article aside time, .night-mode article aside .creator {
        color: #a3acb1; }
      .night-mode article aside p {
        color: white; }
        .night-mode article aside p.advisory {
          color: #e88063; } }
