*********************/
/*********************
VARIABLES
*********************/
article.document {
  background-color: white;
  /* Track */
  /* Handle */
  /* Handle on hover */ }
  article.document::-webkit-scrollbar {
    width: 0.3em; }
  article.document::-webkit-scrollbar-track {
    background: transparent; }
  article.document::-webkit-scrollbar-thumb {
    background-color: #dae3e8;
    border-radius: 4px; }
  article.document::-webkit-scrollbar-thumb:hover {
    background: #bbcbd5; }
  @media screen {
    article.document {
      max-width: 704px;
      overflow-y: scroll;
      overscroll-behavior-y: contain;
      -webkit-overflow-scrolling: touch;
      padding: 0px 68px 0px 30px;
      box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33); } }
  @media screen and (max-width: 640px) {
    article.document {
      padding: 0px 30px; } }
  article.document h1 {
    font-size: 56px;
    line-height: 53px;
    margin: 24px 0px; }
    @media (max-width: 640px) {
      article.document h1 {
        font-size: 26px;
        line-height: 28px; } }
  article.document h2 {
    font-weight: 600;
    font-size: 1.8em;
    margin-top: 36px;
    line-height: 1.2em; }
  article.document h3 {
    text-transform: capitalize;
    margin-top: 0px; }
  article.document .genre {
    margin: 24px 0px 18px 0px; }
    article.document .genre a {
      display: inline-block;
      text-transform: uppercase;
      font-weight: 600;
      color: white;
      background-color: #231f20;
      padding: 6px 12px;
      border-radius: 2px;
      text-decoration: none; }
  article.document.canceled h1 {
    text-decoration: line-through; }
  article.document .media-gallery {
    margin-top: 26px;
    margin-left: -30px;
    margin-right: -68px; }
    @media (max-width: 640px) {
      article.document .media-gallery {
        margin-right: -30px; } }
  article.document p {
    font-size: 18px;
    line-height: 28px; }
    article.document p.advisory {
      color: #e88063; }
  article.document .time-address {
    color: #575e62;
    font-size: 1rem;
    font-weight: 400; }
    article.document .time-address address {
      display: inline-block;
      font-style: normal; }
    article.document .time-address time {
      display: inline-block; }
      article.document .time-address time:first-letter {
        text-transform: capitalize; }
  article.document .cols {
    display: flex;
    margin-top: 12px; }
    article.document .cols aside.meta {
      width: 25%;
      margin-top: 25px;
      padding-right: 12px; }
    article.document .cols main {
      width: 75%; }
    @media (max-width: 640px) {
      article.document .cols {
        display: block; }
        article.document .cols aside.meta, article.document .cols main {
          width: 100%; } }
  article.document .actions {
    position: sticky;
    top: 8px;
    float: right;
    transform: translateX(60px); }
    @media (max-width: 640px) {
      article.document .actions {
        display: inline-block;
        position: fixed;
        right: 8px;
        transform: translateX(0px);
        float: none;
        display: block; } }
    article.document .actions button {
      background-color: rgba(0, 0, 0, 0.4); }
      article.document .actions button i {
        color: white; }
  @media print {
    article.document a[href*='//']:after {
      content: " (" attr(href) ") ";
      color: black; }
    article.document p {
      page-break-inside: avoid; }
    article.document .actions {
      display: none; } }
  article.document[lang="ar"] /deep/ aside.related-articles ul {
    padding-left: inherit;
    padding-right: 0px; }
  article.document[lang="ar"] aside.meta {
    padding-left: 12px;
    padding-right: 0px; }

.night-mode article.document {
  /* Handle on hover */ }
  .night-mode article.document::-webkit-scrollbar-thumb {
    background-color: #141213;
    border-radius: 4px; }
  .night-mode article.document::-webkit-scrollbar-thumb:hover {
    background: #060606; }
  @media screen {
    .night-mode article.document {
      background-color: #231f20; }
      .night-mode article.document h1, .night-mode article.document h2, .night-mode article.document h3 {
        color: #eee; }
      .night-mode article.document h1 {
        line-height: 56px;
        letter-spacing: -1.2px; } }
    @media screen and (max-width: 640px) {
      .night-mode article.document h1 {
        line-height: 31px; } }
  @media screen {
      .night-mode article.document address, .night-mode article.document time {
        color: #a3acb1; }
      .night-mode article.document p {
        color: white; }
        .night-mode article.document p.advisory {
          color: #e88063; }
      .night-mode article.document .genre {
        margin-bottom: 12px; }
        .night-mode article.document .genre a {
          display: inline-block;
          text-transform: uppercase;
          font-weight: 600;
          color: #231f20;
          background-color: #eee;
          padding: 6px 12px;
          border-radius: 2px;
          text-decoration: none; } }
