@media print {
  nav {
    display: none; } }

nav a {
  color: #14c6ab;
  font-size: 0.9rem;
  font-weight: 400;
  line-height: 1rem;
  text-decoration: none;
  text-transform: capitalize;
  transition: transform 0.07s ease-out; }
  nav a:hover {
    color: #085247;
    transform: translateX(3px); }
    nav a:hover:before {
      content: ">"; }

nav.vertical a {
  display: block;
  margin-bottom: 15px; }

nav.horizontal {
  overflow-x: auto;
  margin-bottom: 15px; }
  nav.horizontal a {
    display: inline-block;
    margin-right: 15px; }
    nav.horizontal a:before {
      content: ">"; }

@media (max-width: 640px) {
  nav {
    overflow-x: auto;
    overflow-y: hidden;
    margin-bottom: 15px; }
    nav.vertical a, nav.horizontal a {
      display: inline-block;
      margin-right: 15px;
      margin-bottom: 0px; }
      nav.vertical a:before, nav.horizontal a:before {
        content: ">"; } }
